.wrapper {
  aspect-ratio: 5/1.1; /* соотношение сторон */
  background-image: url(../../../public/images/breadcrumb-bg.png);
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
h1 {
  font-size: 48px;
  font-weight: 500;
}
.link_from {
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  color: #000000;
}
.link_to {
  text-decoration: none;
  font-weight: 300;
  font-size: 16px;
  color: #000000;
}
